.alpheios-lookup__form {
  margin: 30px 5px 5px !important;
  text-align: left !important;
}
.alpheios-input.alpheios-lookup__input {
  width: 70% !important;
  margin-bottom: 10px !important;
  vertical-align: top !important;
}
.alpheios-input.alpheios-lookup__input:focus {
  border-color: #5BC8DC !important;
}
.alpheios-lookup__settings {
  text-align: left !important;
}
a.alpheios-lookup__settings-link {
  display: block;
  padding-top: 5px !important;
}
.alpheios-lookup__button_with_link {
  width: 29% !important;
}
.alpheios-panel .alpheios-lookup__form {
  width: 100% !important;
  margin: 5px 0 !important;
}
.alpheios-panel__options-item {
  max-width: none !important;
}
.alpheios-panel__options-item .alpheios-select:not([multiple]):not([size]),
.alpheios-panel__options-item .alpheios-select[multiple],
.alpheios-panel__options-item .alpheios-select[size] {
  max-width: 250px !important;
}
.alpheios-override-lang {
  margin-bottom: 10px !important;
}
.alpheios-override-lang__label {
  padding-bottom: 10px !important;
}