.alpheios-popup {
  display: flex;
  flex-direction: column !important;
  min-width: 300px !important;
  min-height: 150px !important;
  z-index: 1000 !important;
  position: fixed !important;
  left: 200px !important;
  top: 100px !important;
  box-sizing: border-box !important;
  overflow: auto !important;
  border: 1px solid var(--alpheios-border-color) !important;
  border-radius: 0.625rem !important;
}
.alpheios-popup__header {
  display: flex;
  justify-content: space-between !important;
  height: 3.5rem !important;
  background: var(--alpheios-color-muted) !important;
}
.alpheios-popup__logo {
  height: 1.75rem !important;
  width: auto !important;
  margin: 0.875rem 0 0 0.875rem !important;
}
.alpheios-popup__close-btn {
  width: 5rem !important;
  height: 100% !important;
  cursor: pointer !important;
  fill: var(--alpheios-color-neutral-lightest) !important;
  stroke: var(--alpheios-color-neutral-lightest) !important;
  stroke-width: 2.5 !important;
}
.alpheios-popup__close-btn svg {
  position: relative !important;
  top: 50% !important;
  transform: translateY(-50%) !important;
  left: 1rem !important;
  width: 2.75rem !important;
  height: auto !important;
}
.alpheios-popup__close-btn:hover, .alpheios-popup__close-btn:focus {
  fill: var(--alpheios-color-bright-hover) !important;
  stroke: var(--alpheios-color-bright-hover) !important;
  background: var(--alpheios-color-muted-hover) !important;
}
.alpheios-popup__close-btn:active {
  fill: var(--alpheios-color-bright-pressed) !important;
  stroke: var(--alpheios-color-bright-pressed) !important;
  background: var(--alpheios-color-muted-pressed) !important;
}
.alpheios-popup__body {
  display: flex;
  flex-direction: column !important;
  padding: 1.25rem !important;
  background: var(--alpheios-color-neutral-lightest) !important;
}
.alpheios-popup__toolbar {
  position: relative !important;
  display: flex;
  justify-content: space-between !important;
  align-items: center !important;
  margin-bottom: 0.625rem !important;
}
.alpheios-popup__toolbar-text {
  padding-right: 1.25rem !important;
}
.alpheios-popup__toolbar-text[lang=ara] {
  padding: 0px 20px 0px 20px !important;
  /* the arabic amiri font does not like the top padding */
}
h3.alpheios-popup__toolbar-selection {
  display: inline-block;
}
.alpheios-popup__toolbar-buttons {
  display: flex;
  justify-content: flex-end !important;
  flex-wrap: wrap !important;
}
.alpheios-popup__toolbar-button {
  margin: 0 0 0.625rem 0.625rem !important;
}
.alpheios-popup__content {
  padding: 1.25rem !important;
  border: 1px solid var(--alpheios-border-color) !important;
  border-radius: 0.625rem !important;
}
.alpheios-popup__providers {
  margin-left: 2.5rem !important;
}
.alpheios-popup__providers-title {
  font-weight: 700 !important;
  color: var(--alpheios-color-neutral-dark) !important;
}
a.alpheios-popup__providers-link {
  display: inline-block;
  margin-bottom: 0.375rem !important;
  font-weight: 700 !important;
}
.alpheios-popup__definitions--placeholder {
  padding: 0.625rem 0 !important;
}
.alpheios-popup__providers-item {
  color: var(--alpheios-color-neutral-dark) !important;
}
.alpheios-popup__notifications {
  display: none;
  position: relative !important;
  padding: 0.625rem 1.25rem !important;
  background: #BCE5F0 !important;
  flex: 0 0 3.75rem !important;
  overflow: hidden !important;
}
.alpheios-popup__notifications-close-btn {
  position: absolute !important;
  right: 0.3125rem !important;
  top: 0.3125rem !important;
  display: block;
  width: 1.25rem !important;
  height: 1.25rem !important;
  margin: 0 !important;
  cursor: pointer !important;
  fill: #D1D1D0 !important;
  stroke: #D1D1D0 !important;
}
.alpheios-popup__notifications-close-btn:hover,
.alpheios-popup__notifications-close-btn:focus {
  fill: #5BC8DC !important;
  stroke: #5BC8DC !important;
}
[data-notification-visible=true] .alpheios-popup__notifications {
  display: block;
}
.alpheios-popup__notifications--lang-switcher {
  font-size: 12px !important;
  float: right !important;
  margin: -1.25rem 0.625rem 0 0 !important;
  display: inline-block;
}
.alpheios-popup__notifications--lang-switcher .alpheios-select {
  width: 7.5rem !important;
  height: 1.5625rem !important;
}
.alpheios-popup__notifications--important {
  background: #73CDDE !important;
}