.alpheios-morph__formtext {
  font-weight: 700 !important;
}
.alpheios-morph__source {
  font-size: smaller !important;
  color: #4E6476 !important;
  font-style: italic !important;
}
.alpheios-morph__dial {
  font-size: smaller !important;
}
.alpheios-morph__attr {
  font-weight: normal !important;
  padding-right: 0.25em !important;
}
.alpheios-morph__linkedattr {
  color: var(--alpheios-color-vivid) !important;
  cursor: pointer !important;
  font-weight: 700 !important;
}
.alpheios-morph__linkedattr:hover, .alpheios-morph__linkedattr:active {
  text-decoration: underline !important;
}
.alpheios-morph__pofs span:last-child:after {
  content: ";" !important;
}
.alpheios-morph__inflset {
  margin-left: 0.5em !important;
  margin-top: 0.5em !important;
}
.alpheios-morph__inflset h5 {
  display: none;
  font-size: 16px !important;
  line-height: 1 !important;
  margin-bottom: 0.5em !important;
}
.alpheios-morph__inflset:first-child h5 {
  color: #4E6476 !important;
  display: block;
}
.alpheios-morph__morphdata {
  display: inline;
}
.alpheios-morph__inflections, .alpheios-morph__definition, .alpheios-morph__forms {
  margin-left: 0.5em !important;
}
.alpheios-morph__listitem:after {
  content: ", " !important;
}
.alpheios-morph__listitem:last-child:after {
  content: "" !important;
}
.alpheios-morph__list .alpheios-morph__infl:first-child .alpheios-morph__showiffirst {
  display: block;
}
.alpheios-morph__list .alpheios-morph__infl .alpheios-morph__showiffirst {
  display: none;
}
.alpheios-morph__lexemes .alpheios-definition__lemma {
  display: none;
}
div.alpheios-morph__inline {
  display: inline;
}
div.alpheios-morph__block {
  display: block;
}
.alpheios-panel__tab-panel .alpheios-morph__lexemes {
  font-size: 0.75rem !important;
}
.alpheios-morph__inflfeatures span:first-child:before {
  content: "(" !important;
}
.alpheios-morph__inflfeatures span:last-child:after {
  content: ")" !important;
}
h4.alpheios-morph__groupitem {
  display: inline;
  font-weight: 700 !important;
}
.alpheios-morph__groupitem:last-child:after {
  content: ":" !important;
}
.alpheios-morph__dictentry {
  clear: both !important;
  margin-bottom: 1.25rem !important;
}
.alpheios-morph__dictentry .lemma_index,
.alpheios-morph__dictentry .lemma_index_spacer {
  display: inline-block;
  text-align: center !important;
  font-weight: 700 !important;
  margin-right: 0.3125rem !important;
}
.alpheios-morph__dictentry .alpheios-morph__features:before, .alpheios-morph__dictentry .alpheios-morph__features:after {
  content: "" !important;
  display: table;
  clear: both !important;
}
.alpheios-morph__dictentry .alpheios-morph__features p {
  margin-bottom: 0 !important;
  margin-top: 0 !important;
}
.alpheios-morph__dictentry .alpheios-morph__features p.feature_extras {
  font-style: italic !important;
}
.alpheios-morph__dictentry .alpheios-morph__features span.feature_source .alpheios-morph__attr {
  font-size: smaller !important;
}
.alpheios-morph__dictentry .alpheios-morph__morphdata,
.alpheios-morph__dictentry p.feature_extras,
.alpheios-morph__dictentry .alpheios-morph__definition_list,
.alpheios-morph__dictentry .alpheios-morph__translation_list,
.alpheios-morph__dictentry .alpheios-morph__inflections {
  margin-left: 34px !important;
}
.alpheios-morph__dictentry .alpheios-morph__definition_list,
.alpheios-morph__dictentry .alpheios-morph__translation_list,
.alpheios-morph__dictentry .alpheios-morph__inflections {
  /* border-top: 1px solid $alpheios-toolbar-color; */
  margin-top: 5px !important;
  padding-left: 5px !important;
}
.alpheios-morph__dictentry .alpheios-morph__definition_list .block_title,
.alpheios-morph__dictentry .alpheios-morph__translation_list .block_title,
.alpheios-morph__dictentry .alpheios-morph__inflections .block_title {
  margin: 0 !important;
  color: #4E6476 !important;
  font-size: 10px !important;
  text-align: right !important;
}
.alpheios-morph__dictentry .alpheios-morph__definition {
  margin-bottom: 5px !important;
}
.alpheios-morph__dictentry .alpheios-morph__inflset {
  margin-top: 0 !important;
  margin-left: 7px !important;
}
.alpheios-morph__dictentry .alpheios-morph__inflset .alpheios-morph__forms {
  margin-left: 0 !important;
  display: inline-block;
  vertical-align: top !important;
}
.alpheios-morph__dictentry .alpheios-morph__inflset .inflset_index {
  display: inline-block;
  font-weight: bold !important;
  vertical-align: top !important;
}
.alpheios-morph__dictentry .alpheios-morph__definition_list .definition_index {
  display: inline-block;
  font-weight: bold !important;
}
.alpheios-morph__dictentry .alpheios-morph__definition_list .alpheios-definition__short {
  display: inline-block;
}